.contacts-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    background-color: #1E1E1E; /* Same background as Skills section */
    padding: 60px 10%;
    color: #fff;
  }
  
  .contacts-text {
    flex: 1;
    max-width: 38%;
    /* margin: 5% 0px; */
  }
  
  .contacts-small-text {
    font-size: 16px;
    line-height: 1.5;
    color: #b2b2b5; /* Slightly lighter for readability */
  }
  
  .contacts-box {
    flex: 1;
    max-width: 40%;
    padding: 30px;
    border: 2px solid #fff; /* Only border for Contact box */
    border-radius: 10px;
    text-align: left;
  }
  
  .contacts-heading {
    font-size: 20px;
    color: #fff;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
  }
  
  .contact-method {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .contact-icon {
    font-size: 22px;
    margin-right: 15px;
    color: #9f69ff; /* Icon color */
  }
  
  .contact-label {
    font-size: 16px;
    font-weight: bold;
    margin-right: 10px;
  }
  
  .contact-link {
    font-size: 16px;
    color: #9f69ff;
    text-decoration: none;
  }
  
  .contact-link:hover {
    text-decoration: underline;
  }
  