.skills-container {
  background-color: #1e1e1e;  /* Dark background to match the overall theme */
  color: #abb2bf;
  padding: 4rem 10%;  /* Adjusted padding for top/bottom, with 10% left/right margins */
}

.skills-title {
  color: #c778dd;  /* Light purple color for consistency */
  font-size: 2.5rem;
  margin-bottom: 3rem;
  text-align: left;  /* Aligned left as in the image */
  font-weight: bold;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);  /* Three columns for better alignment */
  gap: 2rem;  /* Spacing between the boxes */
}

.skill-box {
  background-color: #1e1e1e;  /* Keep the background consistent with the theme */
  border: 1px solid #abb2bf;  /* Subtle border matching the text color */
  padding: 1.5rem;
  border-radius: 8px;  /* Smooth rounded corners */
  transition: transform 0.3s ease;  /* Animation for hover effect */
  text-align: left;  /* Left align content as in the design */
}

.skill-box h3 {
  font-size: 1.4rem;
  color: #ffffff;  /* White heading text */
  margin-bottom: 1rem;
  font-weight: bold;
}

.skill-box ul {
  list-style: none;  /* Remove bullet points */
  padding: 0;
}

.skill-box li {
  font-size: 1rem;
  margin-bottom: 0.8rem;
  color: #abb2bf;
}

.skills-container::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 80px;
  background-color: #c778dd;
  top: 50px;
  left: 10%;
}

/* For hover effect */
.skill-box:hover {
  transform: translateY(-5px);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);  /* Slight shadow effect */
}
