/* App.css */

/* Import the Fira Code font */
@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Fira Code", monospace;
  background-color: #1e1e1e;
  color: #abb2bf;
}

header > nav {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
/* header > nav {
  display: flex;
  flex-direction: row;

} */

.logo {
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  margin-left: 150px
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin-right: 10%;
}

.nav-links li a {
  color: #abb2bf;
  text-decoration: none;
}

.nav-links li a:hover {
  color: #c778dd;
}

/* Hero Section */
.hero-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  padding: 0 10%;
}

.hero-content {
  max-width: 70%;
  /* border: 1px solid red; */
}

.hero-content h1 {
  font-size: 3rem;
  color: #fff;
}

.hero-content .highlight {
  color: #c778dd;
}

.hero-content p {
  margin: 20px 0;
  font-size: 1.2rem;
}

.contact-btn {
  padding: 10px 20px;
  background-color: transparent;
  color: #c778dd;
  border: 2px solid #c778dd;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s;
}

.contact-btn:hover {
  background-color: #c778dd;
  color: #1e1e1e;
}

/* Photo Box Placeholder */
.photo-box {
  width: 400px;
  height: 500px;
  background-color: #2a2a2a;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #c778dd;
  /* You can replace this with an actual image */
}

/* Quote Section */
.quote-section {
  padding: 50px;
  text-align: center;
}

.quote-section blockquote {
  font-size: 1.5rem;
  color: #fff;
}

.quote-section cite {
  display: block;
  margin-top: 10px;
  font-size: 1.2rem;
  color: #abb2bf;
}
