.about-me-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1E1E1E;
  padding: 50px 10%;
  color: #fff;
}

.about-me-text {
  flex: 1;
  max-width: 60%;
  padding-right: 0px;
}

.about-me-heading {
  color: #C778DD;
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 100px;
}

.about-me-intro {
  font-size: 28px;
  font-weight: bold;
  margin: 30px 0;
}

.about-me-description {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 20px;
}

.about-me-button {
  padding: 10px 20px;
  background-color: transparent;
  color: #c778dd;
  border: 2px solid #c778dd;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s;
  margin-top: 50px;
}

.about-me-button:hover {
  background-color: #c778dd;
  color: #1e1e1e;
}

.about-me-image {
  width: 450px;
  height: 600px; /* Portrait-style height */
  background-color: #292933;  /* Placeholder color */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-top: 20px;
  margin-bottom: 20px;

}
