.projects-section {
    padding: 50px 10%;
    background-color: #1e1e1e;
  }
  
  .section-title {
    color: #c778dd;
    font-size: 2rem;
    margin-bottom: 40px;
  }
  
  .projects-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  
  .project-card {
    background-color: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .project-image {
    width: 100%;
    height: 150px;
    background-color: #3a3a3a;
    border: 2px solid #c778dd;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .project-title {
    color: #fff;
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .company-role, .tech-stack, .project-description, .outcome {
    color: #abb2bf;
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .project-link {
    color: #c778dd;
    text-decoration: none;
    border: 1px solid #c778dd;
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  .project-link:hover {
    background-color: #c778dd;
    color: #1e1e1e;
  }
  