 /* Social Icons Sidebar */
 .social-icons {
    position: fixed;
    top: 20%;
    left: 2%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.social-icons a {
    color: #ABB2BF;
    font-size: 1.5rem;
    transition: color 0.3s ease;
}

.social-icons a:hover {
    color: #C778DD;
}

/* Vertical line */
.line {
    width: 1px;
    height: 100px;
    background-color: #ABB2BF;
    margin-bottom: 20px;
}

.social-icons svg {
    font-size: 24px;
}